html,
body {
  padding: 0;
  margin: 0;
  font-family: Poppins;
}

button {
  background-color: #1e45db;
  color: #fff;
  cursor: pointer;
  border: 2px solid #1e45db;
  border-radius: 4px;
  padding: 8px;
  font-size: 16px !important;
  font-weight: 500 !important;
  transition: box-shadow 300ms ease, transform 300ms ease,
    background-color 300ms ease, -webkit-transform 300ms ease !important;
}

button:hover {
  background-color: #012fb7;
  border-color: #012fb7;
  transform: translate3d(0px, -3px, 0.01px) !important;
}

button:focus,
button:focus-visible {
  border-color: #012fb7;
  outline: -webkit-focus-ring-color auto 0px;
}

