}

.hero .card-left {
  position: relative;
  font-size: 24px;
  font-family: var(--roboto);
  text-align: left;
  padding: 46.5px;
  -webkit-transition: all 0.2s ease 0s;
  -moz-transition: all 0.2s ease 0s;
  transition: all 0.2s ease 0s;
  overflow: visible;
  cursor: pointer;
  background-color: #fff;
  border-radius: 18px;
  padding-top: 60px;
  padding-bottom: 60px;
  margin-bottom: 16px;
}

.hero .card-left:hover,
.hero .card-right:hover {
  -webkit-transform: translateY(-10px) scale(1.05);
  transform: translateY(-10px) scale(1.05);
  overflow: visible;
}

.hero .card-right {
  position: relative;
  font-size: 24px;
  font-family: var(--roboto);
  text-align: right;
  padding: 46.5px;
  -webkit-transition: all 0.2s ease 0s;
  transition: all 0.2s ease 0s;
  overflow: visible;
  cursor: pointer;
  background-color: #fff;
  border-radius: 18px;
  padding-top: 60px;
  padding-bottom: 60px;
}

