.about-hero {
  background-color: #f1f9ff;
  justify-content: center;
  display: flex;
  align-items: center;
  padding-top: 37px;
  padding-bottom: 97px;
}

.about-hero .hero-head {
  padding-left: 5px;
  padding-right: 5px;
}

.about-hero p {
  font-size: 40px;
  font-family: var(--poppins);
  font-weight: 500;
  text-align: center;
  color: #201f23;
  line-height: 1.4;
}

.about-hero .sub-heading {
  font-size: 15px;
  font-family: var(--poppins);
  text-align: center;
  color: #686781;
}

.about-hr {
  color: #c4c4c4;
  height: 1.5px;
  border-width: 0;
  background: rgb(104 183 129 / 15%);
  margin-top: 5rem;
  margin-bottom: 5rem;
  display: block;
  width: 100%;
}

.about-card {
  background-color: #fff;
  -webkit-box-shadow: 0px 2px 14px rgb(0 0 0 / 10%);
  box-shadow: 0px 2px 14px rgb(0 0 0 / 10%);
  border-radius: 20px;
  padding: 30px;
  min-width: 200px;
  text-align: center;
}

.about-card .number {
  font-size: 50px;
  font-weight: 700;
  color: #1e45db;
  font-family: var(--poppins);
}

.about-card .desc {
  font-size: 13px;
  font-weight: 400;
  color: #686781;
  font-family: var(--poppins);
}

.team {
  padding-top: 80px;
  padding-bottom: 80px;
}

.team .team-heading {
  font-family: var(--poppins);
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 60px;
}

.team-heading span {
  color: #1e45db;
}

.team-card {
  background-color: #fff;
  border-radius: 16.58px;
  padding-top: 25px;
  padding-right: 20px;
  padding-left: 20px;
  justify-content: center;
  display: flex;
  mix-blend-mode: luminosity;
}

.team-card-text {
  background-color: #fff;
  border-radius: 16.58px;
  justify-content: center;
  display: flex;
}

.team-card-text .team-text {
  font-size: 60px;
  font-weight: 700;
  padding: 71px;
}

.team-name {
  text-align: center;
  font-size: 24px;
  font-family: var(--poppins);
  font-weight: 600;
  margin-bottom: 8px;
}

.team-role {
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  font-family: var(--poppins);
}

.social-section {
  padding-top: 80px;
  padding-bottom: 60px;
}

.social-section h1 {
  font-size: 48px;
  font-weight: 600;
  font-family: var(--poppins);
  margin-bottom: 10px;
  margin-top: 12px;
  line-height: 54.29px;
}

.social-section .desc {
  font-size: 20px;
  font-family: var(--poppins);
  font-weight: 500;
  margin-bottom: 24px;
}

.join-us button {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: -0.01em;
  color: #ffffff;
  -moz-box-flex: 0;
  flex: none;
  -moz-box-ordinal-group: 1;
  order: 0;
  flex-grow: 0;
  /* margin: 0px 10px; */
  background: #1e45db;
  border-radius: 5px;
  border-color: transparent;
  padding: 16px 32px;
}

.join-us button:hover {
  background-color: #012fb7;
  border-color: #012fb7;
}

.vision {
  padding-bottom: 100px;
  padding-top: 120px;
}

.vision .pr-0 {
  padding-right: 0;
}

.vision .pt {
  padding-top: 15px;
}

.vision-heading {
  line-height: 52.5px;
  font-weight: 700;
  font-family: var(--poppins);
}

.vision .sub-heading {
  font-size: 20px;
  font-weight: 600;
  color: #1e45db;
  font-family: var(--poppins);
}

.vision .sub-heading-desc {
  font-size: 32px;
  font-weight: 600;
  line-height: normal;
  color: #201f23;
  font-family: var(--poppins);
}

.vision .gallery {
  padding-bottom: 60px;
}

.timeline {
  padding-top: 80px;
  padding-bottom: 88px;
}

.timeline .timeline-heading {
  text-align: center;
  margin-bottom: 70px;
}

.timeline_line {
  position: absolute;
  left: 48%;
  top: 10px;
}

.timeline-line {
  height: 694px;
  background: #1e45db;
  width: 6px;
  border-color: transparent;
  border-radius: 4px;
  margin-top: -30px;
  position: relative;
}

.line-gray {
  position: absolute;
  width: 6px;
  border-color: transparent;
  height: 595px;
  left: 50.48%;
  z-index: -1;
  top: 44rem;
  background: #c4c4c4;
}

.timeline .bottom-line {
  padding-left: 5px;
}

.timeline .date-1 {
  position: absolute;
  top: 21%;
  left: 45%;
  background: white;
  font-size: 28px;
  font-weight: 500;
}

.timeline .date-2 {
  position: absolute;
  top: 39%;
  left: 47.5%;
  background: white;
  font-size: 28px;
  font-weight: 500;
}

.timeline .date-3 {
  position: absolute;
  top: 56.5%;
  left: 47.5%;
  background: white;
  font-size: 28px;
  font-weight: 500;
}

.timeline .date-4 {
  position: absolute;
  top: 74.5%;
  left: 47.5%;
  background: white;
  font-size: 28px;
  font-weight: 500;
}

.timeline-pt {
  padding-top: 120px;
  padding-left: 96px;
  padding-right: 96px;
}

.timeline .sub-heading-right {
  font-size: 20px;
  font-weight: 700;
  font-family: var(--poppins);
  text-align: left;
}

.timeline .sub-heading-left {
  font-size: 20px;
  font-weight: 700;
  font-family: var(--poppins);
  text-align: right;
}

.timeline .desc-right {
  font-size: 16px;
  font-family: var(--poppins);
  text-align: left;
}

.timeline .desc-left {
  font-size: 16px;
  font-family: var(--poppins);
  text-align: right;
}

.timeline-heading span {
  color: #1e45db;
}

.timeline-ending {
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  padding-top: 7px;
  font-family: var(--poppins);
}

.timeline .mob-date {
  display: none;
}

.about-mt {
  position: absolute;
  left: 0rem;
  top: 1rem;
  width: 100%;
}

.mob-bottom-line {
  display: none;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .about-hero {
    padding-bottom: 54px;
    padding-top: 18px;
  }
  .about-hero .container {
    padding-left: 0;
    padding-right: 0;
  }
  .about-mt {
    margin-top: 24px;
    position: relative;
    left: 0;
    top: 0;
  }
  .about-hero p {
    font-size: 20px;
    font-weight: 500;
  }
  .about-hero .sub-heading {
    font-size: 12px;
  }
  .about-hr {
    display: none;
  }
  .about-card {
    padding: 20px;
    margin-top: 8px;
    min-width: auto;
  }
  .about-px-4 {
    padding-right: 4px;
    padding-left: 4px;
  }
  .about-card .number {
    font-size: 24px;
  }
  .about-card .desc {
    font-size: 7px;
    font-weight: 400;
    color: #686781;
    font-family: var(--poppins);
  }
  .vision .sub-heading-desc {
    font-size: 16px;
    font-weight: 600;
    line-height: 30px;
    color: #201f23;
    font-family: var(--poppins);
    text-align: center;
    margin-bottom: 16px;
  }
  .vision .sub-heading {
    font-size: 15px;
    text-align: center;
    line-height: 22px;
    font-weight: 600;
    color: #1e45db;
    font-family: var(--poppins);
  }
  .vision .gallery {
    padding-bottom: 32px;
  }
  .vision {
    padding-bottom: 24px;
    padding-top: 24px;
  }
  .team {
    padding-top: 40px;
    padding-bottom: 40px;
  }
  .team .team-heading {
    font-size: 16px;
    margin-bottom: 20px;
  }
  .team-card {
    padding-top: 20px;
  }
  .team-name {
    font-size: 14px;
    margin-bottom: 4px;
  }
  .team-role {
    font-size: 12px;
  }
  .team-card-text .team-text {
    font-size: 32px;
    font-weight: 700;
    padding: 46px;
  }
  .social-section {
    padding-top: 22px;
    padding-bottom: 54px;
  }
  .social-section h1 {
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    font-family: var(--poppins);
    margin-bottom: 10px;
    margin-top: 12px;
    line-height: 24px;
  }
  .social-section .desc {
    font-size: 14px;
    font-family: var(--poppins);
    font-weight: 400;
    margin-bottom: 24px;
    text-align: center;
  }
  .join-us {
    text-align: center;
  }
  .vision .pt {
    padding-top: 8px;
  }
  .vision .pl {
    padding-left: 8px;
    padding-right: 0px;
  }
  .vision .pl-0 {
    padding-left: 0;
  }
  .vision-heading {
    line-height: 30px;
    font-size: 16px;
    font-weight: 600;
  }
  .timeline {
    padding-top: 17px;
    padding-bottom: 44px;
  }
  .timeline .timeline-heading {
    text-align: center;
    margin-bottom: 9px;
    font-size: 18px;
  }
  .timeline-img {
    display: none;
  }
  .timeline .desc-right {
    font-size: 11.5px;
    font-family: var(--poppins);
    text-align: center;
  }
  .timeline .sub-heading-right {
    font-size: 16px;
    font-weight: 700;
    font-family: var(--poppins);
    text-align: center;
  }
  .timeline-pt {
    padding-left: 0;
    padding-right: 0;
    padding-top: 0;
    margin-top: 70px;
    background: white;
  }
  .timeline .sub-heading-left {
    font-size: 16px;
    font-weight: 700;
    font-family: var(--poppins);
    text-align: center;
  }
  .timeline .desc-left {
    font-size: 11.5px;
    font-family: var(--poppins);
    text-align: center;
  }
  .timeline_line {
    position: absolute;
    left: 50%;
    top: 10px;
  }
  .timeline-line {
    height: 712px;
    background: #1e45db;
    width: 4px;
    border-color: transparent;
    margin-top: 32px;
    position: relative;
    border-radius: 0px;
  }
  .mob-bottom-line {
    text-align: center;
    display: block;
  }
  .idea-mt {
    margin-top: 0;
  }
  .timeline .date-1,
  .timeline .date-2,
  .timeline .date-3,
  .timeline .date-4,
  .line-gray,
  .bottom-line {
    display: none;
  }
  .timeline .mob-date {
    text-align: center;
    font-size: 20px;
    font-weight: 500;
    line-height: 29px;
    display: block;
    margin-top: 4px;
  }
  .timeline-ending {
    text-align: center;
    font-size: 15px;
    font-weight: 700;
    padding-top: 7px;
    font-family: var(--poppins);
  }
}
