:root {
  --anim-hover-out: hover-out-fake;
}

.r-anm-o {
  animation-name: hover-in;
  animation-duration: 500ms;
  animation-fill-mode: both;
}

.r-anm {
  animation-name: hover-out;
  animation-duration: 500ms;
  animation-fill-mode: both;
}

@keyframes hover-in {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-180deg);
    --anim-hover-out: hover-out;
  }
}

@keyframes hover-out {
  0% {
    transform: rotate(-180deg);
  }
  100% {
    transform: rotate(0deg);
    --anim-hover-out: hover-out;
  }
}

@keyframes drop-anm {
  0% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
    --anim-hover-out: hover-out;
  }
}
