
.file_upload{
    /* display:none; */
    /* visibility:hidden; */
    position: relative;
    position: relative;
    width: 50px;
    height: 50px;
    visibility: hidden;
    


}
.file_upload::before{
    content: '';
    position:absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 50%;
    height: 50%;
    visibility: visible;
   
    background-image: url('https://www.freeiconspng.com/thumbs/camera-icon/camera-icon-21.png');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    border-radius: 100%;
    background-color: #012FB7
    ;

    /* display:none; */
  
    
}
.uploader_button{
    position: relative;
}
.uploader_button .file_upload {
    position: absolute;
    right: 0;
    bottom: -15px;
}

